<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn 
        depressed
        v-on="on"
        class="mr-3"
        color="primary">
        <v-icon class="mr-3">mdi-plus-circle</v-icon>
        Tambah data
      </v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Tambah Unit
      </v-card-title>
      <v-divider></v-divider>
      <v-form v-model="valid" ref="form">
        <v-card-text class="pa-6 pb-0">
          <v-text-field
            v-model="dataUnit.nama"
            label="Nama unit"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-text-field
            v-model="dataUnit.kepala"
            label="Kepala unit"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-text-field
            v-model="dataUnit.hp"
            label="No Hp/WA"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-text-field
            v-model="dataUnit.alamat"
            label="Alamat kantor"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-autocomplete
            v-model="dataUnit.prov"
            :items="items"
            label="Provinsi"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-autocomplete>
          <v-autocomplete
            v-model="dataUnit.kab"
            :items="items"
            label="Kabupaten"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-autocomplete>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        dataUnit: {
          nama: '',
          kepala: '',
          hp: '',
          alamat: '',
          prov: '',
          kab: ''
        },
        items: ['foo', 'bar', 'zee'],
        requiredRule: [v => !!v || 'Data harus diisi']
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          console.log(this.dataUnit)
        }
      }
    }
  }
</script>
