<template>
  <v-card class="ma-md-6 ma-sm-3">
    <v-card-title class="py-2">
      <v-icon class="mr-3">mdi-office-building</v-icon>
      Daftar Unit
    </v-card-title>
    <v-divider></v-divider>
    <v-card-text>
      <TambahUnit/>
      <KosongkanUnit/>
      <v-form ref="form" v-model="valid" class="mt-6">
        <v-row dense>
          <v-col cols="12" md="4" sm="6" class="py-0">
            <v-autocomplete
              label="Provinsi"
              outlined
              dense
              v-model="dataFilter.provinsi"
              :items="dataProv"
              item-text="nama"
              item-value="id"
              required
              :rules="requiredRule"
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="4" sm="6" class="py-0">
            <v-autocomplete
              label="Kabupaten"
              outlined
              dense
              v-model="dataFilter.kabupaten"
              :items="dataKab"              
              item-text="nama"
              item-value="id"
              :disabled="dataFilter.provinsi === ''?true:false"
              required
              :rules="requiredRule"
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="4" sm="6" class="py-0">
            <v-btn
              type="submit"
              :disabled="!valid"
              @click.prevent="validate"
              color="primary"
              depressed>
              filter
            </v-btn>
          </v-col>
        </v-row>
      </v-form>

      <DataUnit/>
    </v-card-text>
  </v-card>
</template>

<script>
  import { mapState } from 'vuex'
  import DataUnit from '@/components/Unit/DataUnit'
  import TambahUnit from '@/components/Unit/TambahUnit'
  import KosongkanUnit from '@/components/Unit/KosongkanUnit'

  export default{
    data: () => ({
      valid: true,
      requiredRule: [v => !!v || 'Data harus diisi'],
      data: ['foo'],
      dataFilter: {
        provinsi: '',
        kabupaten: '',
        jenjang: ''
      }
    }),

    components: {
      KosongkanUnit,
      TambahUnit,
      DataUnit
    },

    created() {
      this.$store.dispatch('dataTempat/getDataProv')
      this.$store.dispatch('dataTempat/updateIsLoading', true)
    },

    computed: {
      ...mapState('dataTempat', ['dataProv', 'dataKab', 'isLoading'])
    },

    watch: {
      'dataFilter.provinsi': function (val) {
      this.$store.dispatch('dataTempat/getDataKab', val)        
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          console.log(this.dataFilter)
        }
      }
    }
  }
</script>
